.details{
    /* background-color: #0F5132; */
    position: absolute;
    background: #15141484;
    width: 20vw;
    border-radius: 20px 0 20px 20px;
    bottom: -420px;
    right: 34px;
    z-index: 1;
    padding: 16px;

    .user{
        padding: 30px 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;
        border-bottom: 1px solid #dddddd35;

        img{
            width: 100px;
            height: 100px;
            border-radius: 50%;
            object-fit: cover;
        }
        p{
            display: flex;
            align-items: center;
            text-align: center;
        }
    }

    .info{
        padding: 20px;
        display: flex;
        flex-direction: column;
        gap: 25px;

        .option{

            .title{
                display: flex;
                align-items: center;
                justify-content: space-between;

                img{
                    width: 30px;
                    height: 30px;
                    background-color: rgba(17, 25, 40, 0.3);
                    padding: 10px;
                    border-radius: 50%;
                    cursor: pointer;
                }
            }

            .photos{
                display: flex; 
                flex-direction: column;
                gap: 20px;
                margin-top: 20px;

                .photoItem{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    .photoDetail{
                        display: flex;
                        align-items: center;
                        gap: 20px;


                        img{
                            width: 40px;
                            height: 40px;
                            border-radius: 5px;
                            object-fit: cover;
                        }

                        span{
                            font-size: 14px;
                            color: lightslategray;
                            font-weight: 300;
                        }
                    }

                    .icon{
                        width: 30px;
                        height: 30px;
                        background-color: rgba(17, 25, 40, 0.3);
                        padding: 10px;
                        border-radius: 50%;
                        cursor: pointer;
                    }
                }
            }
        }
        button{
            padding: 10px;
            background-color: rgba(230, 75, 105, 0.523);
            color: azure;
            border: none;
            border-radius: 5px;
            cursor: pointer;

            &:hover{
                background-color: rgba(212, 24, 24, 0.84);
            }
            &.logout{
                padding: 10px;
                background-color: rgba(13, 68, 204, 0.884);
            }
        }
    }
}