.chat{
    flex:2;
    height: 100%;
    display: flex;
    flex-direction: column;
    border-left: 2px solid #dddddd35;
    border-right: 2px solid #dddddd35;
    position: relative;

    .calls{
        position: absolute; /* This will position the calls div absolutely within the chat component */
        top: 0; /* Center vertically */
        left: 0;
        right: 0;
        bottom: 0; /* Center horizontally */
        transform: translate(-20%, -20%); 
        z-index: 1000; 
        display: flex;
        flex-direction: row;
        background-color: #292424df;
        padding: 20px;
        border-radius: 10px;
        align-items: center;
        justify-content: space-between;
        cursor: grabbing;

        video{
            height: 300px;
            width: 400px;
            border-radius: 10px;
        }
    }

    .incoming-call{
        position: absolute;
        top: 40%;
        bottom: 0;
        left: 20%;
        right: 0;
        height: 200px;
        width: 300px;
        background-color: rgba(33, 32, 32, 0.727);
        border-radius: 10px;
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        gap: 50px;

        .user-info{
            margin-top: 20px;
            display: flex;
            flex-direction: row;
            gap: 10px;
            align-items: center;
            margin-left: 10px;

        img{
            height: 50px;
            width: 50px;
            border-radius: 50%;
            object-fit: cover;
        }

        span{
            font-size: 16px;
            color: azure;
        }
    }

        .hangup{
            background-color: azure;
            border-radius: 50%;
            height: 50px;
            width: 50px;
            margin-left: 120px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
    
            img {
                height: 30px;
                width: 30px;
                border-radius: 50%;
                border: none;
                object-fit: cover;
            }
        }


    }

    .top{
        padding: 16px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        border-bottom: 1px solid #dddddd35;
        gap: 30px;

        .user{
            display: flex;
            align-items: center;
            gap: 20px;     
            
            img{
                width: 50px;
                height: 50px;
                border-radius: 50%;
                object-fit: cover;
            }
            .texts{
                display: flex;
                flex-direction: column;
                gap: 5px;

                span{
                    font-size: 18px;
                    font-weight:bold;
                }
                p{
                    font-size: 14px;
                    font-weight: 300;
                    color: #a5a5a5;
                }
            }
        }
        .icons{
            display: flex;
            align-items: flex-end;
            cursor: pointer;
            gap: 20px;

            img{
                width: 20px;
                height: 20px;
            }
        }
        
    }
    .center{
        padding: 20px;
        flex: 1;
        flex: 1;
        overflow-y: scroll;
        display: flex;
        flex-direction: column;
        border-radius: 10px;
        gap: 20px;
        &::-webkit-scrollbar {
            width: 8px; 
          }

        .message{
            display: flex;
            gap: 20px;
            max-width: 56%;
            align-self: flex-start;

            &.own{
                align-self: flex-end;


                .texts{
                    p{
                        text-align: justify;
                        background-color:  #0F5132;
                        border-radius: 10px;
                        font-size: 14px;
                        padding: 10px 16px;

                        word-wrap: break-word;
                    }
                    .date{
                        text-align: left;
                    }
                    span{
                        text-align: right;
                        font-size: 12px;
                        color: silver;
                    }
                }
            }
            img{
                width: 60px;
                height: 60px;
                border-radius: 10px;
                object-fit: cover;
            }
            video{
                width: 60px;
                height: 60px;
                border-radius: 10px;
                object-fit: cover;
            }

            .texts{
                display: flex;
                flex-direction: column;
                gap: 5px;
                max-width: 100%;
                width: fit-content;
                img{
                    width: 100%;
                    height: 300px;
                    border-radius: 20px;
                    object-fit: cover;
                }
                video{
                    width: 100%;
                    height: 300px;
                    border-radius: 20px;
                    object-fit: cover;
                }

                p{
                    text-align: justify;
                    padding: 10px 16px;
                    background-color: rgba(128, 128, 128, 0.786);
                    border-radius: 10px;
                    font-size: 14px;
                    width: auto;
                    max-width: 100%;
                    word-wrap: break-word;
                    word-break: break-word;
                    overflow-wrap: break-word;
                }
                .date{
                    text-align: left;
                }
                .messageDelete{
                    max-width: 50%;
                    padding: 10px;
                    border-radius: 10px;
                    border: none;
                    outline: none;
                    background-color: rgba(17, 18, 16, 0.737);
                    color:white;
                    font-weight: 600;
                    cursor:pointer;
                    &:hover{
                        background-color: rgba(17, 18, 16, 0.697);
                        color:red;
                    }
                }
                span{
                    text-align: right;
                    font-size: 12px;
                    color: silver;
                }
            }
        }
    }
    .bottom{
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;   
        border-top: 1px solid #dddddd35;
        gap: 20px;
        margin-top: auto;
        
        .icons{
            display: flex;
            gap: 20px;
        }
        img{
            width: 20px;
            height: 20px;
            cursor: pointer;
        }
        input{
            flex:1;
            background-color: rgba(0, 0, 0, 0.445);
            border: none;
            outline: none;
            color:azure;
            padding: 10px;
            border-radius: 10px;
            font-size: 16px;
        }
        
        &.disabled{
            background-color: rgba(13, 32, 25, 0.944);
            cursor: not-allowed;
        }
        .emoji{
            position: relative;

            .picker{
                position: absolute;
                bottom: 50px;
                right:10px;
                }
            }

        .sendButton{
            background-color: rgba(5, 148, 5, 0.97);
            color: azure;
            padding: 10px 20px;
            border: none;
            border-radius: 10px;
            cursor: pointer;
        }

        &.disabled{
            background-color: rgba(5, 148, 5, 0.568);
            cursor: not-allowed;
        }
    }
    }

    