.chatList{
    flex:1;
    overflow-y: scroll;
    overflow-x: hidden;
    &::-webkit-scrollbar {
        width: 8px; 
      }

    .search{
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 16px;

        .searchBar{
            flex: 1;
            background-color: rgba(0, 0, 0, 0.445);
            display: flex;
            flex:1;
            align-items: center;
            gap: 20px;
            border-radius: 25px;
            padding: 16px;
            cursor:text;

            input{
                background-color: transparent;
                border:none;
                outline: none;
                color:azure;
                display: flex;
                flex:1;
            }

            img{
                width: 20px;
                height: 20px;
            }
        }
        .add{
            width: 36px;
            height: 36px;
            background-color: rgba(0, 0, 0, 0.499);
            padding: 10px;
            border-radius: 10px;
            cursor: pointer;
        }
    }
    .item{
        display: flex;
        align-items: center;
        gap: 20px;
        padding: 20px;
        cursor: pointer;
        border-bottom: 1px solid #dddddd35;

        img{
            width: 50px;
            height: 50px;
            border-radius: 50%;
            object-fit: cover;
        }
        .texts{
            display: flex;
            flex-direction: column;
        }
        span{
            font-weight: 500;
        }
        p{
            font-style: 14px;
            font-weight: 300;
        }
    }
}